.block-inner{
  width: 80%;
  height: 80%;
  background-color: #e3e4e6;
  border-radius: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: all 1s linear;
  -o-transition: all 1s linear;
  transition: all 1s linear;
}
.block-inner.alive{
  background-color: #9f9;
}
.block-inner>.popover{
  display: none;
}


